@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700&display=swap');

/* CSS RESET */
*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

/* APP */

body {
  background-color: #0c0733d7;
  background-image: url("https://www.transparenttextures.com/patterns/washi.png");
  font-family: 'Rubik', sans-serif;
}

/* container */

.container {
  max-width: 1200px;
  margin: 0 auto;
}

/* todos list */

.title {
  text-align: center;
  font-size: 4rem;
  padding: 3rem 0 1rem;
  color: rgb(255, 255, 255);
  text-shadow: 2px 4px 8px rgba(0, 0, 0, 0.5);
}
.todos {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

.todo {
  display: flex;
  justify-content: space-between;
  padding: 1rem 1.25rem;
  color: white;

  background-color: rgba(0, 0, 0, 0.7);
  width: 50%;
  font-size: 1.4rem;
  border-radius: 1rem;
  transition: all 0.2s;

  &:hover {
    background-color: rgba(0, 0, 0, 0.6);
  }
}

.mutations {
  display: flex;
  gap: 0.75rem;
}

.todo__status,
.todo__delete {
  border: none;
  padding: 0;
  line-height: 1;
  cursor: pointer;
  transition: all 0.2s;
  background: none;
  color: rgb(255, 255, 255);
  font-size: 1.6rem;
  &:hover {
    transform: scale(1.15);
  }
}

.todo__status {
  font-size: 1.75rem;
}


/* form */
.form {
  display: flex;
  gap: 0.5rem;
  width: 50%;
  margin: 1rem auto;
}

.form__input {
  flex: 1;
  padding: 1rem 0.75rem;
  border-radius: 0.25rem;
  border: none;
  outline: none;

  &:focus,
  &:hover {
    background-color: rgba(255, 255, 255, 0.85);
  }
}

.form__button {
  border: none;
  background-color: rgba(12, 7, 51, 0.8);
  cursor: pointer;
  color: white;
  border-radius: 0.25rem;
  position: relative;
  overflow: hidden;
  padding: 0.75rem 1.25rem;
  font-size: 1.2rem;
}

.form__button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -150%;
  width: 150%;
  height: 100%;
  background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.4) 50%, rgba(255, 255, 255, 0) 100%);
  transform: skewX(-20deg);
  animation: smoothShine 1.8s ease-in-out infinite;
}

@keyframes smoothShine {
  0% {
    left: -150%;
    opacity: 0;
  }

  10% {
    opacity: 0.2;
  }

  90% {
    opacity: 0.3;
  }

  100% {
    left: 150%;
    opacity: 0;
  }
}

.form__button:hover {
  background-color: rgba(12, 7, 51, 0.7);
}



@media (max-width: 640px) {
  .title {
    font-size: 3.5rem;
  }

  .form {
    width: 95%;
  }

  .todo {
    width: 95%;
    font-size: 1.2rem;
  }
}